.line {
  fill: none;
  stroke: steelblue;
  stroke-width: 6px;
  opacity: 0.6;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
.line:hover {
  opacity: 1;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  cursor: pointer;
  stroke-width: 10px;
}

.graph {
  background: #eeeeee;
  padding: 0 40px 20px;
  margin: 0 0 20px;
  overflow: visible;
}



