.grid line {
  stroke: lightgrey;
  stroke-opacity: 0.7;
  shape-rendering: crispEdges;
}
.single-line {
  fill: none;
  stroke: steelblue;
  stroke-width: 3px;
  opacity: 1;
}

.graph {
  background: #eeeeee;
  padding: 0 40px 20px;
  margin: 0 0 20px;
}



